import React, { useEffect } from 'react'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import styled from 'styled-components'

const StyledMenu = styled.div`
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transform: ${props =>
    props.isMenuOpen ? 'translateX(0)' : 'translateX(-100%)'};
  background-color: rgba(0, 0, 0, 1);
  transition: transform 0.3s ease-in-out;
  z-index: 1;
`

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  color: white;

  & ul {
    display: flex;
    flex-direction: row;
    margin-bottom: 1rem;
    overflow: hidden;

    @media (max-width: 768px) {
      flex-direction: column;
    }

    li {
      font-size: 45px;
      cursor: pointer;
      margin: 0;
      font-weight: 100;
      line-height: 1.2;
      overflow: hidden;

      &:not(:last-child) {
        margin-right: 0.5rem;
      }

      &:nth-child(odd) {
        span {
          transform: translateY(105%);
        }
      }

      &:nth-child(even) {
        span {
          transform: translateY(-105%);
        }
      }

      span {
        overflow: hidden;
        display: inline-block;

        &:not(:last-child) {
          margin-right: 1rem;
        }

        &:nth-child(1) {
          transition: transform 0.3s ease-in-out 300ms, opacity 0.3s ease-in-out;
        }

        &:nth-child(2) {
          transition: transform 0.3s ease-in-out 450ms, opacity 0.3s ease-in-out;
        }
      }

      &.show span {
        opacity: 1;
        transform: translateY(0);
      }
    }

    li {
      transition: opacity 0.3s ease-in-out;
    }

    &:hover li {
      opacity: 0.5;
    }

    & li:hover {
      opacity: 1;
    }
  }

  .overlay-left {
    width: 66.6%;
    height: 100%;
    padding: 5rem 0 0 3rem;
    position: relative;

    @media (max-width: 768px) {
      width: 100%;
    }
  }

  .overlay-right {
    width: 33.3%;
    background: #099fc0;
    height: 100%;
    margin-bottom: 0;

    @media (max-width: 768px) {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100vw;
      height: 10rem;
      z-index: -1;
    }
  }

  .overlay-info {
    position: absolute;
    bottom: 1rem;
    left: 2rem;
    color: white;
    font-size: 12px;
    line-height: 0.5;
    font-weight: 100;

    a {
      text-decoration: none;
      color: white;
    }

    .animation-container {
      overflow: hidden;
      margin-bottom: 0;
      margin-bottom: 0.5rem;
    }

    p {
      transition: transform 0.3s ease-in-out;
      overflow: hidden;
      margin: 0;
      line-height: 1;

      &:nth-child(1) {
        transition-delay: 300ms;
      }

      &:nth-child(2) {
        transition-delay: 450ms;
      }
      &:nth-child(3) {
        transition-delay: 600ms;
      }
      &:nth-child(4) {
        transition-delay: 750ms;
      }

      &.up {
        transform: translateY(-100%);
      }

      &.down {
        transform: translateY(100%);
      }

      &.show {
        transform: translateY(0);
      }
    }
  }
`

const StyledLink = styled(AniLink)`
  text-decoration: none;
  color: white;
  background: black;
`

const OverlayMenu = ({ isMenuOpen, setIsMenuOpen }) => {
  useEffect(() => {
    isMenuOpen
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'unset')
  }, [isMenuOpen])

  return (
    <StyledMenu isMenuOpen={isMenuOpen}>
      <Wrapper isMenuOpen={isMenuOpen}>
        <div className="overlay-left">
          <nav>
            <ul className="overlay-menu">
              <li className={`${isMenuOpen && 'show'}`}>
                <span>
                  <StyledLink cover bg={'black'} direction="right" to="/">
                    Home /
                  </StyledLink>
                </span>
              </li>
              <li className={`${isMenuOpen && 'show'}`}>
                <span>
                  <StyledLink cover bg={'black'} direction="right" to="/blog/">
                    Blog
                  </StyledLink>
                </span>
              </li>
            </ul>
          </nav>
          <div className="overlay-info">
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} up`}>Los Angeles, CA</p>
            </div>
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} down`}>
                <a href="mailto:info@liquidswords.design">
                  info@liquidswords.design
                </a>
              </p>
            </div>
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} up`}>(213) 974-3211</p>
            </div>
            <div className="animation-container">
              <p className={`${isMenuOpen && 'show'} down`}>
                Copyright 2020  Liquid Swords, LLC
              </p>
            </div>
          </div>
        </div>
        <div className="overlay-right"></div>
      </Wrapper>
    </StyledMenu>
  )
}

export default OverlayMenu
