import React, { useState } from 'react'
import styled from 'styled-components'
import { useStaticQuery, graphql } from 'gatsby'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import Img from 'gatsby-image'

const NavLink = styled(AniLink)`
  color: white;
  font-size: 1rem;
  font-weight: ${props => props.fontWeight || 'normal'};
  line-height: 1;
  margin: 0 0.5rem 0 0;
  padding: 0.25rem;
  text-decoration: none;

  &.current-page {
    border-bottom: 2px solid white;
    font-weight: 700;
  }

  &:last-of-type {
    margin-right: 0;
  }
`

const StyledHeader = styled.header`
  background: ${props => (props.isMenuOpen ? 'transparent' : 'black')};
  position: fixed;
  width: 100%;
  margin-bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem;
  z-index: 2;

  nav {
    margin-bottom: 0;
  }
`

const Header = ({ setIsMenuOpen, isMenuOpen }) => {
  const [clickable, setClickable] = useState(true)

  // Ensure transitions are (mostly) finished before allowing another toggle
  function handleMenuToggle() {
    if (!clickable) return

    setIsMenuOpen(!isMenuOpen)
    setClickable(false)

    setTimeout(() => {
      setClickable(true)
    }, 750)
  }

  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "logo.png" }) {
        childImageSharp {
          fixed(width: 150) {
            width
            height
            src
            srcSet
          }
        }
      }
    }
  `)
  return (
    <StyledHeader isMenuOpen={isMenuOpen}>
      <NavLink cover bg={'black'} direction="right" to="/">
        <Img
          fixed={data.placeholderImage.childImageSharp.fixed}
          backgroundColor="black"
          fadeIn={false}
        ></Img>
      </NavLink>
      <nav>
        <a
          className={`site-header__toggle menu-toggle js-menu-toggle ${
            isMenuOpen ? 'menu-is-open' : ''
          }`}
          data-drawer="menu"
          type="button"
          onClick={() => handleMenuToggle()}
        >
          <span className="menu-toggle__bars"></span>
        </a>
      </nav>
    </StyledHeader>
  )
}

export default Header
